import Vue from 'vue'
import Router from 'vue-router'
//import main from '@/components/main'

Vue.use(Router)

export default new Router({
  mode: 'history',  
  routes: [
    {
      path: '/',
      name: 'init',
      component: resolve=>require(['@/components/init'],resolve)   
    },
    {
      path: '/home',
      name: 'home',
      component: resolve=>require(['@/components/Home'],resolve)     
    }    
  ]
})